
import XTabbar from '@/components/tabbar.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Grid from 'vux/src/components/grid/grid.vue'
import GridItem from 'vux/src/components/grid/grid-item.vue'
import Badge from 'vux/src/components/badge/index.vue'
import Swiper from 'vux/src/components/swiper/swiper.vue'

import { getProfile, getCoustemURL, logout } from '@/api/user'
import { removeData } from '@/utils/auth'
export default {
name: 'userCenter',
data () {
return {

menuList: [],

userinfo: {},
loginouttoast: false,
list: [
{
url: 'javascript:',
img: 'static/images/banner.png',
title: '送你一朵fua'
},
{
url: 'javascript:',
img: 'static/images/banner.png',
title: '送你一次旅行'
}
]
}
},
watch: {
userinfo () {
console.log(this.userinfo)
this.menuList = [
{
name: '待付款',
picurl: 'static/images/pending_pay.png',
num: this.userinfo.orderinfo.waittingpay,
type: 1
},
{
name: '待收货',
picurl: 'static/images/pending_received.png',
num: this.userinfo.orderinfo.sent,
type: 2
},
{
name: '待评价',
picurl: 'static/images/pending_comment.png',
num: this.userinfo.orderinfo.recive,
type: 3
},
{
name: '退款/售后',
picurl: 'static/images/customerServe.png',
num: this.userinfo.orderinfo.refund,
type: 4
}
]

},




$route () {
console.log('路由监听')
this.getProfile()
}
},
created () {
this.getProfile()
},
methods: {
close () {
this.loginouttoast = false
},
surebtn () {
logout()
.then(res => {
if (res.data.errorcode === 0) {

this.$router.push({
path: '/home'
})

removeData()
} else {
this.$vux.toast.text(res.data.errormsg, 'middle')
}
})
.catch(err => {
console.log(err)
this.$vux.toast.text('网络异常！', 'middle')
})
},
handleLoginout () {
this.loginouttoast = true
},
clickService () {
const _this = this
const postData = {}
getCoustemURL(postData)
.then(res => {
if (res.data.errorcode === 0) {

window.location.href = res.data.data.url
} else {
_this.$vux.toast.text(res.data.errormsg, 'middle')
}
})
.catch(err => {
_this.$vux.loading.hide()
console.log(err)
_this.$vux.toast.text('网络异常！', 'middle')
})
},

getProfile () {
getProfile()
.then(res => {
if (res.data.errorcode === 0) {

this.userinfo = res.data.data
} else {
this.$vux.toast.text(res.data.errormsg, 'middle')
}
})
.catch(err => {
console.log(err)
this.$vux.toast.text('网络异常！', 'middle')
})
},
goOrderList (type) {
if (type === 3) {

this.$router.push({
name: 'uc-comments',
query: {
type: 0
}
})
} else {
this.$router.push({
name: 'myOrder',
query: {
type: type
}
})
}
}
},
components: {
Group,
Cell,
Grid,
GridItem,
Badge,
Swiper,
XTabbar
}
}



